@use 'vars.scss' as *;
@use 'colors.scss' as *;

.error-container {
    position: absolute;

    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);

    display: flex;
    flex-direction: column;

    align-items: center;

    gap: 10px;

    border-top: 10px solid $border-color;
    border-inline: 1px solid $border-color;
    
    border-radius: $border-radius-small;

    padding: 20px;

    box-shadow: 5px 0px 3px $border-color;
}


.back-button {
    position: fixed;
    
    top: 0px;
    left: 0px;
    
    margin: 20px;
}